import React, { useState, useEffect } from "react";
import { authService, firebaseInstance } from '../../DataBase'
import { Link, Redirect } from "react-router-dom"
import styled from 'styled-components'
import logo from '../../assets/branging/logo.svg'
import './cursors.css'





const Auth = () => {
    

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [newAccount, setNewAccount] = useState(false);
    const [isLoggedIn, setIsLoggedIn] = useState(false)
    const [error, setError] = useState("");

    useEffect(()  => {
        authService.onAuthStateChanged((user) => {
          if(user){
            setIsLoggedIn(true);
          } else {
            setIsLoggedIn(false);
          }
        })
      }, []);


    const reload = () =>{
        window.location.reload()
        }

    const onChange = (event) => {
        const {
            target:{ name, value },
        } = event;
        if (name === "email") {

            setEmail(value);

        } else if (name === "password") {

            setPassword(value);
        }
    };
    
    const onSubmit = async(event) => {

        event.preventDefault();

        try {

            if(newAccount) {
                await authService.createUserWithEmailAndPassword(
                    email, password

                )
            }else { 
                await authService.signInWithEmailAndPassword(
                    email, password
                )

            }

        } catch(error){
            console.log(error);
                setError(error.message)

        }
       
    };
    const toggleAccount = () => { 
        setNewAccount((prev) => !prev);
    }
    const onSocialClick = (event)=>{
        const{
            target: { name },
        } = event;

        let provider
        if(name === "google"){
            provider =new firebaseInstance.auth.GoogleAuthProvider();
        }
        if(name === "github"){
            provider = new firebaseInstance.auth.GithubAuthProvider();
        }
        authService.signInWithPopup(provider);
    }
    return (

        <Container>
            {isLoggedIn ? <Redirect to="/code"/>:null}
             <div onClick={reload}>
               <Link to='/' > <img className="logo" src={logo} /> </Link>
               
            </div>

            <Titulo>{newAccount ? <h1 className="register">Register</h1> : <h1 className="login">Log In</h1>}</Titulo>

            <form onSubmit={onSubmit}>

            <h1>E-mail</h1>
                <input 
                className="userInput"     
                name="email" 
                type="text"  
                required 
                value={email} 
                onChange={onChange}
                />
            <h1> Password <Link className="link-forgot-password">{newAccount ? null : "Forgot password?" }</Link> </h1>
                <input 
                className="userInput"
                name="password" 
                type="password" 
                required 
                value={password} 
                onChange={onChange}
                />  

                <input 
                className={newAccount ? "RegisterButton" : "LoginButton"} 
                type="submit" 
                value={newAccount ? "Register" : "Log In"}/> 

            </form>
       

            <button onClick={onSocialClick} name="google">Continue with Google</button>
            <button  onClick={onSocialClick} name="github">Continue with GitHub</button>
            {error}
            <hr></hr>
            
        <span 
        className="div-new-to-create-account"
        onClick={toggleAccount}>

            {newAccount ? 
            <h1>You denuevo?</h1> 
            : 
            <h1> Dont have account? Pick to create account </h1>}
            
        </span>
          
    </Container> 

    )
}

const Container = styled.div`
margin-left: auto;
margin-right: auto;

font-style: normal;
font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
margin-top: 70px;
background-color:#fff;
.logo{
    height: 50px;
    width: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
h1{  
  text-align: center;
  color: black;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -0.5px;
  line-height: 36px;
  text-decoration: none solid rgb(36, 41, 46);
  text-align: center;
  height: 36px;
  display: block;
}
form{
    text-align: justify;
    font-size: 14px;
    line-height: 21px;
    text-decoration: none solid rgb(36, 41, 46);
    word-spacing: 0;
    background-color: #f6f8fa;
    border-radius: 5px;
    border: solid 1px rgb(204, 204, 204);
    margin: 16px 0 0 0;
    padding: 20px 20px 20px 20px;
    height: 228.6px;
    width: 270px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    input{
        width: 266px;
        height: 32px;
        line-height: 20px;
        text-decoration: none solid rgb(36, 41, 46);
        word-spacing: 0px;
        border: 1px solid #e1e4e8;
        margin: 5px 0 15px 0;
        display: block;
        cursor: text;
        box-sizing: border-box;
        box-shadow: rgba(255, 228, 232, 0.2) 0px 1px 0px 0px inset;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
    }
    h1 {
        font-size: 14px;
        height: 20.8;
        text-align: left;
        margin: 0 0 -10px 0;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 21px;
        text-decoration: none solid rgb(36, 41, 46);
        display: block;
        width: 266px;
      }
      h1 .link-forgot-password {
        float: right;
        text-decoration: none;
        color: blue;
        font-size: 12px;
      }
      h1 .link-forgot-password:hover {
        color: rgb(4, 4, 204);
        text-decoration: underline;
      }
      .userInput {
        outline: none;
        box-sizing: border-box;
        padding-left: 15px;
      }
      .userInput:focus{
        box-shadow: 1px 1px 3px 3px#54b4b4;
      }
      .RegisterButton {
        font-family: "Press Start 2P";
        box-shadow: 3px 5px #206f33;
        letter-spacing: 1px;
        background: #2ea44f;
        font-size: 14px;
        font-weight: 500px;
        line-height: 20px;
        text-decoration: none solid rgb(255, 255, 255);
        text-align: center;
        white-space: nowrap;
        color: white;
        /*BOX*/
        height: 32px;
        width: 266px;
        border: 0px solid;
        border-radius: 5px;
        margin: 40px 0 0 0;
        padding: 5px 16px 5px 16px;
        position: relative;
        display: block;
        outline: none;
      }
      .RegisterButton:active {
        color: #da6964;
        background-color: #98342f;
        box-shadow: 0 2px #da6964;
       
    }
      .LoginButton{
        font-family: "Press Start 2P";
        box-shadow: 3px 5px #002e83;
        letter-spacing: 1px;
        background: #0097d3;
        font-size: 14px;
        font-weight: 500px;
        line-height: 20px;
        text-decoration: none solid rgb(255, 255, 255);
        text-align: center;
        white-space: nowrap;
        color: white;
        /*BOX*/
        height: 32px;
        width: 266px;
        border: 0px solid;
        border-radius: 5px;
        margin: 40px 0 0 0;
        padding: 5px 16px 5px 16px;
        position: relative;
        display: block;
        outline: none;

      }
      .LoginButton:active {
        color: #da6964;
        background-color: #98342f;
        box-shadow: 0 2px #da6964;
       
    }
      .submit:active {
       
        border: none;
        color: #2ea44f;
        background-color: #206f33;
        box-shadow: 0 2px #2ea44f;
      }
      .div-new-to-create-account {
        border: solid 0.5px black;
        text-align: justify;
        font-size: 14px;
        line-height: 21px;
        text-decoration: none solid rgb(36, 41, 46);
        word-spacing: 0;
        background-color: transparent;
        border-radius: 5px;
        border: solid 1px rgb(204, 204, 204);
        margin: 16px 0 0 0;
        padding: 20px 20px 20px 20px;
        width: 270px;
        display: block;
        margin-left: auto;
        margin-right: auto;
      }
      
}

`;
const Titulo = styled.h1`

h1 {
    
    font-family: "Press Start 2P";
    
}
.login{
    color:#0097d3;
}
.register{
    color:#50a64e;
}

`;

export default Auth