import React, {Component} from 'react' ;
import './Menu.css'
import icon from'../../../assets/menu.svg'
import xicon from '../../../assets/menu-x.svg'
import DropMenu from './dropMenu/DropMenu'

class Menu extends Component {
    state={
        dropMenu: false,
        srcicon: icon,
        with: window.innerWidth,
    }

    changeIcon = () => {
        if (this.state.srcicon===icon){
            this.setState({srcicon:xicon})
            this.setState({dropMenu:true})
            
        } 
        if(this.state.srcicon===xicon){
            this.setState({srcicon:icon})
            this.setState({dropMenu:false})
            
        }
        
  
    }
    
    handleWindowSizeChange = () => {
        this.setState({ with:window.innerWidth })
    }

    //hooks
    componentDidMount(){
        window.addEventListener('resize', this.handleWindowSizeChange)
    }
    componentWillUnmount(){
        window.removeEventListener('resize', this.handleWindowSizeChange)
    }
    render(){
        const { width } = this.state;
        const isMovile = width <= 600;

        return(
            <div>
                <button  onClick={this.changeIcon} className="button-hamburgerIcon">
                    <img className="img-hamburger" src={this.state.srcicon} />
                </button>  
                {this.state.dropMenu ? (<DropMenu/>) : (null)

                }  
                
     
            </div>
        );
    }
}
export default Menu