import React, { Component } from 'react';
import {Link} from 'react-router-dom'

import './Navbar.css'
import Menu from './menu/Menu'
import LogoNavbar from './logoNavbar/LogoNavbar'
import ButtonStart from '../buttonStart/ButtonStart'
import LearnInput from '../buttonLearn/learnInput'

//links
import ResourcesLink from '../../pages/resources/link/resourcesLink'

const linkStyle ={
    textDecoration: 'none',
    color:'#fff',
}


class Navbar extends Component {
    state = { 
        width: window.innerWidth,
    }
    
    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth })
      }
    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange)
      }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange)
      }
     

    render() {
        const { width } = this.state;
        const isMovile = width <= 600;
        const navElemen = {
            logo: <LogoNavbar/>,
            menu: <Menu/>,
            learnInput: <LearnInput/> 
        }
      
        if(isMovile){
            return (    
                <div className="header-navbar" >

                    <div className="navbarDiv1">
                        {navElemen.menu}

                    </div>
                    <div className="navbarDiv2">
                        <div className="borderClick">
                                {navElemen.logo}

                        </div>
                    </div>       
                    <div className="navbarDiv3"> 
                        <div className="borderClick">                      
                            {navElemen.learnInput} 
                        </div>   
                    </div>
        
                </div> );

        } else {
            //desktop version ------->
            return (    
                <div className="header-navbar">

                    <div className="navbarDiv1">
                        <div className="borderClick">
                            {navElemen.logo}

                        </div>

                    </div>
                    <div className="navbarDiv15">
                        <div className="resources-div"><ResourcesLink/></div>
                    </div>
                    <div className="navbarDiv2">
                  
                        <div className="borderClick">
                        {navElemen.learnInput}  

                        </div>  

                    </div>
                    <div className="navbarDiv3">
                        {navElemen.menu}    

                    </div>
   
                 </div> )
        }
    
    }
}
 
export default Navbar;


