import React, { Component,useEffect } from 'react';
import './ButtonStart.css'
import { Link } from 'react-router-dom'

//Public functions
function reload(){
    window.location.reload()
}

const ButtonStart = () => {

 
        return (
            
         <div className="div-ButtonStart" onClick={reload}>

             <Link to='/login' className="Link-start"style={{ textDecoration: 'none' }}> 

                <input  className="ButtonStart" type="button" value="START"/> 

            </Link>

         </div>
           
        )
        
 

    
}
 
export default ButtonStart;


