import {Link} from 'react-router-dom'
import React, { Component } from 'react';
import './DropMenu.css'
//import icons
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import CloudIcon from '@material-ui/icons/Cloud';
import CodeIcon from '@material-ui/icons/Code';
import ButtonStart from '../../../buttonStart/ButtonStart'
//sound hook
//import links
import ResoursesLink from '../../../../pages/resources/link/resourcesLink'
import {authService} from '../../../../DataBase.js'

const linkStyle ={
    textDecoration: 'none',
    color:'#fff',
}





class DropMenu extends Component {
    state = { 
        width: window.innerWidth,
       
     }
     handleWindowSizeChange = () => {
         this.setState({ with:window.innerWidth })
     }
     componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange)
        
      }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange)
      }
    
    render() { 
        const { width } = this.state;
        const isMovile = width <= 600;
        const links = {
            support: <Link className="suport" style={linkStyle} >  <HelpOutlineIcon className="icons-menu" /> <b>Support</b> </Link>,
            resourses: <ResoursesLink/>,
            buttonStart: <ButtonStart/> ,
            moreContent:  <Link className="morecontent" style={ linkStyle }> <CloudIcon className="icons-menu-cloud" /> More content </Link>,
            about: <Link className="about" to="/about" style={linkStyle} >  About </Link>,
            donate:<Link className="donate" style={linkStyle}> Donate</Link>,
        }
     
        if(isMovile){

            return(
                
                <div className="div-movile">  
    
                        <div className="li" > 
                            {links.support}

                        </div>
                        <div className="li">
                            {links.resourses}

                        </div>
                        <div className="li-start">
                           <div className="button">
                                {links.buttonStart}

                           </div>
                            
                        </div>
                        <div className="li">
                            {links.moreContent}

                        </div>
                        <div className="li">
                        {links.about}

                        </div>
                        <div className="li" id="div-donate">
                            {links.donate}

                        </div>

                        <div className="separador" ></div>

                 </div>
                
            )
        } else {
            return(
                
                <div className="div-movile">  
    
                        <div className="li" > 
                            {links.support}

                        </div>
                   
                        <div className="li-start">
                             <div className="button"  >
                                {links.buttonStart}
                            </div>

                        </div>
                        <div className="li">
                            {links.moreContent}

                        </div>
                        <div className="li">
                        {links.about}

                        </div>
                        <div className="li" id="div-donate">
                            {links.donate}

                        </div>

                        <div className="separador" ></div>

                 </div>
                
            )
        }
        

        
            
         
  

        
        
    }
}
 
export default DropMenu;