import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './learnInput.css'
//icons
import SchoolIcon from '@material-ui/icons/School'


class LearnInput extends Component {
    state = { 
        width: window.innerWidth
     }
     handleWindowSizeChange = () => {
         this.setState({ width: window.innerWidth })
     }
     componentWillMount() {
         window.addEventListener('resize',this.handleWindowSizeChange)
     }
     componentWillUnmount() {
         window.removeEventListener('resize', this.handleWindowSizeChange)
     }

    render() { 
        const { width } = this.state
        const isMovile = width <= 600
        if(isMovile){
            return(
                <div >
               <Link to='/code' style={{ textDecoration: 'none' }}> <button className="learn-code-input" > <SchoolIcon/> </button> </Link> 
               </div>
            )
            
        } else {   
            return (
                <div>
                    <Link to='/code'style={{ textDecoration: 'none' }}> <button className="learn-code-input"> Code <SchoolIcon/> </button> </Link>
                </div>
               
              )
        }
        
    }
}
 
export default LearnInput;